.header p{
	margin:0;
	height: .8rem;
	line-height: .8rem;
	border-bottom: 0.01rem solid #ccc;
}
.header p a{
	display: inline-block;
	width:33.33%;
	text-align: center;
	background: #fff;
	border-right:0.01rem solid #ccc;
	color: #000;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.header p a:nth-of-type(3){
	border:none;
}
.header div{
	background:#fff;
	height: .8rem;
	padding:0 .2rem;
}
.header div span{
	line-height:.8rem;
}

.header select{
	height:.8rem;
	border:none;
	font-size: .rem;
	background:#fff;
}
option{
	font-size: 0.12rem;
	
}


