.form{
	padding:0 .2rem;
}
.tops{
	height:.8rem;
	line-height:.8rem;
	background: #108EE9;
	color:#fff;
	text-align:center;
	font-size:.35rem;
}
.form>span{
	display: inline-block;
	width:1.5rem;
	text-align: right;
	height: .7rem;
	line-height: .7rem;
	margin:.2rem 0;
}
.form input[type=text],.form input[type=password]{
	height:.7rem;
	border: none;
	border:0.01rem solid #ccc;
	width:5rem;
	font-size: .3rem;
	padding-left: .1rem
}

.btns{

	width: 90%;
	margin-left: 5%;
	border:none;
	margin-bottom: .2rem;
	text-align:center;
}


