.comhead{
	float: left;
	width: .8rem;
	height:.8rem;
	margin-right: .1rem;
	margin-top: -.1rem;
	border-radius: 50%;

}
.foot{
	font-size: .25rem;
	background:#eee;
	margin: 0;
	height:1rem;
	overflow: hidden;

}
.foot span{
	margin:.2rem;
}
.enter{
	width:100%;
	position: fixed;
	bottom: 0;
	left:0;
}
.enter input{
	width:80%;
	border:none;
	height:.8rem
}
.enter button{
	width:20%;
	height:.8rem;
	border:none;
}

.imgs img{
	width:100%;
	margin-top: .2rem;
}



