.text h4{
	padding:0;
	margin:0 0 .2rem 0;
	background: #fff;
	border-bottom: 0.01rem solid #ccc;
}
.text h4 span{
	width:28%;
	border-right: 0.01rem solid #ccc;
	padding:0 .2rem;
	margin-right: .1rem;
	line-height: 1rem;
	font-weight: 200;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

}
.text h4 input{
	border:none;
	height: .8rem;
	width: 70%;
}

textarea{
	width:97%;
	margin-left:1%;
	height:6rem;
	resize:none;
	border:none;
}

.pop{
	visibility: hidden;
	position:fixed;
	top:1rem;
	left:1rem;
	width:5rem;
	overflow:hidden;
	padding:.2rem;
	background: #B2ADAD;
	color: #fff;
	border-radius: .2rem;
}

.pop ul{
	margin:0;
	padding:0;
	
}
.pop li{
	float: left;
	list-style: none;
	padding:.2rem 0;
	margin:0 .15rem ;
}
.pop li:hover{
	color:blue;
}

.RichEditor-editor .public-DraftEditor-content {
    height: 3rem;
}




